.sendMessageBox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #8a36c7;
    border-radius: 1rem 1rem 0 0;
    padding: 1rem;
}

.boxMessage {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 5rem;
}

.boxText {
    width: 100%;
    height: 100%;
    outline: none;
    border:none;
    background-image:none;
    background-color:transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

.fileInput {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: row;
}

.fileInput label {
    align-items: center;
    justify-content: center;
    display: flex;
    width: 2rem;
    height: 2rem;
    color: gray;
    border-radius: 10rem;
    transition: 0.5s;
}

.fileInput label:hover {
    background-color: #cacaca;
}

.sendButton {
    width: 2rem;
    height: 2rem;
    border-radius: 10rem;
    color: gray;
    transition: 0.5s;
    outline: none;
    border:none;
    background-color: white;
}

.sendButton:hover {
    background-color: #cacaca;
}

.audioButton {
    width: 2rem;
    height: 2rem;
    border-radius: 10rem;
    color: rgb(88, 88, 88);
    transition: 0.5s;
    outline: none;
    border:none;
    background-color: #cacaca;
}

.audioButton:hover {
    background-color: #b1b1b1;
}

.boxRespond {
    display: flex;
    flex-direction: row;
    color: rgb(88, 88, 88);
    background-color: #b1b1b1;
    padding: 0.5rem;
    width: 80%;
    border-radius: 1rem 1rem 0 0;
}

.boxRespond p {
    width: 100%;
}

.boxRespond button, .tt {
    width: 1.25rem;
    height: 1.25rem;
    border-radius: 10rem;
    color: rgb(88, 88, 88);
    transition: 0.5s;
    outline: none;
    border:none;
    background-color: #cacaca;
}

.boxRespond button:hover {
    background-color: #727272;
}


/**/
.button {
    display: flex;
    flex-direction: column;
    padding: 0.25rem;
    margin: 0.5rem;
    cursor: move;
    user-select: none;
    transition: 0.25s ease;
    width: 70%;
}

.left {
    background-color: gray;
    color: white;
    border-radius: 1rem 1rem 1rem 0;
}

.left h5, .left h6 {
    color: #cfcfcf;
}

.left h6 {
    width: 100%;
    text-align: right;
}

.right {
    margin-left: auto;
    background-color: #8a36c7;
    color: white;
    border-radius: 1rem 1rem 0 1rem;
}

.right h5, .right h6 {
    color: #cfcfcf;
}

.right h6 {
    width: 100%;
    text-align: right;
}

.datas {
    width: 100%;
    text-align: center;
}

.buttonDown {
    position: fixed;
    width: 2rem;
    height: 2rem;
    outline: none;
    border: none;
    color: white;
    background-color: #8a36c7;
    margin: 10px;
    border-radius: 5rem;
}

/**/
.button img {
    pointer-events:none;
}

.respond {
    display: block;
}

.close {
    display: none;
}

.chat {
    display: flex;
    flex-direction: column-reverse;
    overflow-y: scroll;
    overflow-x: hidden;
}

.deslizar {
    animation-name: deslizar;
    animation-duration: 4s;
    animation-fill-mode: forwards;
}

@keyframes deslizar{
    0%   { background-color: red; }
    100% { background-color: gray; }
}